	module.exports = {"data":{"wordpressPost":{"title":"The most popular nail polish on Pinterest is &#8230;","content":"<p>We love a big, bold nail look and we&#8217;re even into a bit of nail art. You know what we mean &#8211; those designs where there&#8217;s a splash of sparkling glitter on one finger or even a geometric pattern of some kind.</p>\n<p>That kind of thing just screams <strong>artistic cool girl</strong> to us and who doesn&#8217;t want a piece of that?</p>\n<figure id=\"attachment_1600\" style=\"width: 212px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-1600\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/15173701/Essie-bottle-JPG-212x300.jpg\" alt=\"\" width=\"212\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/15173701/Essie-bottle-JPG-212x300.jpg 212w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/15173701/Essie-bottle-JPG.jpg 223w\" sizes=\"(max-width: 212px) 100vw, 212px\" /><figcaption class=\"wp-caption-text\">Say hello to your new favourite shade. Essie&#8217;s Topless and Barefoot.</figcaption></figure>\n<p>&nbsp;</p>\n<p>When we&#8217;re not up for the full nail-art thing though (you know &#8211; too busy, or even more likely, too lazy) we like to rock a bold red or, during the chillier months, a rich, deep maroon. And we know you do too.</p>\n<p>Given that we were a bit shocked to discover that<strong> Pinterest&#8217;s most popular nail polish colour of 2017</strong> is <a href=\"http://www.essie.com.au/Colours.aspx\">Essie&#8217;s Topless and Barefoot</a> &#8211; a milky, hint of pink shade that borders on nude.</p>\n<p>Correct! Not unicorn-themed or rainbow-coloured as is so much of the makeup found on Pinterest, not even look-at-me red or rock-chic black, but NUDE.</p>\n<figure id=\"attachment_1601\" style=\"width: 300px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-1601\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/15173733/Essie-collection-JPG-300x270.jpg\" alt=\"\" width=\"300\" height=\"270\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/15173733/Essie-collection-JPG-300x270.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/15173733/Essie-collection-JPG.jpg 394w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Of course, there are plenty more nudes to almost nudes to choose from if you fancy!</figcaption></figure>\n<p>&nbsp;</p>\n<p>Admittedly, 2017 is only in its infancy and nail trends might turn around (and many, many times) before the year is through. Right now, however,  this sweet, hint of beige is where it&#8217;s at. If you like the sound of that then you&#8217;ll need to hurry &#8211; <a href=\"https://www.adorebeauty.com.au/essie/essie-spring-collection-topless-barefoot-soft-beige-pink.html\">Topless and Barefoot</a> is currently selling like hot cakes.</p>\n<p>If you can&#8217;t track down a bottle, but you fancy getting on board with this trend, you might like to consider the Essie shades <a href=\"https://www.priceline.com.au/essie-nail-color-13-5-ml\">Fiji, Sand Tropez or Masterplan</a> &#8211; they&#8217;re all equally as subtle and we think, every bit as pretty.</p>\n<p>NB: Mind you, if all that nude subtlety makes your inner wild girl sad, pop a little glitter or a bold black line over the top! We won&#8217;t judge. In fact, we&#8217;ll only love you more.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/15173816/Essie-JPG.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"81e86e72-c429-54fa-9064-3c948e1b90dc"}}

